import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// store,export
const store =  new Vuex.Store({
    //strict:true,//
    // ()
    state:{
         token:sessionStorage.getItem('token')
    },

    // mutations 
    mutations:{
     updateToken(state, token) {
          state.token = token
     }
    },
    //
    getters:{
         
     },
  });
  
  export default store;


